/*=====Banner What we do=====*/
.banner-what-we-do {
  position: relative; }
  .banner-what-we-do::before {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: #000;
    opacity: 0.3; }
  .banner-what-we-do .views-field-nothing {
    position: absolute;
    font-size: 33px;
    line-height: 40px;
    color: #fff;
    top: 50px;
    left: 115px;
    max-width: 600px; }
  .banner-what-we-do .views-field-field-hero-image img {
    max-height: 210px;
    -o-object-fit: cover;
    object-fit: cover; }

.main-container.container {
  max-width: 1200px;
  width: 100%; }

/*=====Header=====*/
.container {
  padding: 0;
  width: 1200px; }

.navbar-collapse.collapse {
  padding-bottom: 100px;
  position: relative; }
  .navbar-collapse.collapse #block-searchheader {
    position: absolute;
    width: 100%;
    bottom: 20px; }
  .navbar-collapse.collapse .block-search-header {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex; }
    .navbar-collapse.collapse .block-search-header .search-show,
    .navbar-collapse.collapse .block-search-header .search-text {
      height: 60px;
      color: #706e6e; }
      .navbar-collapse.collapse .block-search-header .search-show > *,
      .navbar-collapse.collapse .block-search-header .search-text > * {
        height: 60px; }
    .navbar-collapse.collapse .block-search-header .search-show {
      margin-right: 28px;
      width: 570px;
      position: relative;
      display: -webkit-box;
      display: -webkit-flex;
      display: -ms-flexbox;
      display: flex; }
      .navbar-collapse.collapse .block-search-header .search-show input {
        min-width: 570px;
        border: 1px solid #dbdbdb;
        -webkit-box-shadow: none;
        box-shadow: none;
        padding-left: 20px;
        font-size: 14px; }
      .navbar-collapse.collapse .block-search-header .search-show button {
        background: #a36d2f;
        border: 1px solid #a36d2f;
        -webkit-box-shadow: none;
        box-shadow: none;
        font-size: 0;
        position: absolute;
        width: 60px;
        top: 0;
        right: 0; }
    .navbar-collapse.collapse .block-search-header .search-text {
      min-width: 278px;
      margin-right: 15px;
      background: #ededdc; }
      .navbar-collapse.collapse .block-search-header .search-text p {
        padding: 23px 15px;
        font-size: 14px;
        line-height: 1; }
      .navbar-collapse.collapse .block-search-header .search-text:last-child {
        margin-right: 0; }

